    get isPageAuth() { return this.$route && this.$route.path.includes('auth'); }
    onRouteChange(val) {
        try {
            if (this.isRootPage) {
                this.openMenu();
                this.logOutIsShown = false;
                this.menuIsShown = true;
            }
            else {
                if (this.isPageAuth) {
                    this.logOutIsShown = false;
                    this.menuIsShown = false;
                }
                else if (this.$route.name) {
                    this.logOutIsShown = true;
                    this.menuIsShown = true;
