import { __decorate } from "tslib";
import Vue from 'vue';
import { Mixin } from 'vue-mixin-decorator';
let MsgBoxTools = class MsgBoxTools extends Vue {
    constructor() {
        super(...arguments);
        this.msgBoxIsShown = false;
        this.contentDefault = 'Похоже, сервер не отвечает';
    }
    created() {
        document.addEventListener('keydown', this.keydownHandler);
    }
    beforeDestroy() {
        document.body.classList.remove('modal-open');
        document.removeEventListener('keydown', this.keydownHandler);
    }
    keydownHandler(evt) {
        if (evt.key === 'Escape' && this.msgBoxIsShown)
            this.closeMsgBox();
    }
    openMsgBox() {
        document.body.classList.add('modal-open');
        this.msgBoxIsShown = true;
    }
    closeMsgBox() {
        document.body.classList.remove('modal-open');
        this.msgBoxIsShown = false;
    }
};
MsgBoxTools = __decorate([
    Mixin
], MsgBoxTools);
export default MsgBoxTools;
